.login-page{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255 255 255);
    /* background-image: url(https://i.ibb.co/rtPCkcZ/Untitled-design.png); */
    background-image: url(/static/media/login.5876cebb.svg);
    background-position: 100% 69%;
    background-repeat: no-repeat;
    background-size: 706px;
}

.login-container{
    padding: 10px;
    border: none;
    background: #6d64641a;
    height: 450px;
  width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
