    border-radius: 15px;
}

.registration{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login-header {
    font-size: 24px;
    padding: 24px;
    text-align: center;
    padding-bottom: 40px;
  }
  .button-block {
    padding: 8px 0;
  }
  .button {
    width: 100px;
    padding: 8px;
    background-color: #40444f;
  }
  .button:hover{
    background-color: black;
  }
  .input-spaces {
    padding: 12px 0;
  }
  .registration form {
    text-align: center;
  }
  input[type="email"] {
    border-radius: 4px;
    background-color: white;
    width: 250px;
    height: 30px;
    color: black;
    border-color: white;
  }
  input[type="password"] {
    border-radius: 4px;
    background-color: white;
    width: 250px;
    height: 30px;
    color: black;
    border-color: white;
  }
  input[type="submit"] {
    margin-top: 40px;
    border-radius: 8px;
    background-color: #616878;
    color: ivory;
    width: 100px;
    height: 30px;
  }
  
  input[type="submit"]:hover {
    background-color: #40444f;
  }
.navbar {
  background-color: #292828;
  height: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 20;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
  
}

.nav-logo {
  color: #ffffff;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.5rem;
  flex-grow: 1;
}
i{
    margin-left: 15px;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #21f5e3;
}

.nav-item .active {
  color: #21f5e3;
  border: 1px solid #21f5e3;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1f5156;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-item .active {
    color: #ffdd40;
    border: none;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #ffdd40;
  }
}

  
