* {font-family: 'Open Sans', sans-serif;}

.rwd-table {
  margin: auto;
  min-width: 800px;
  max-width: 100%;
  border-collapse: collapse;
  box-shadow: 0 0 11px rgba(0,0,0,0.5);

}

.rwd-table tr:first-child {
  border-top: none;
  background: #000000;
  color: #fff;
}

.rwd-table tr {
  border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    background-color: #f5f9fca8;
    font-weight: bold;
}

.rwd-table tr:nth-child(odd):not(:first-child) {
  /* background-color: #ebf3f9; */
}

.rwd-table th {
  display: none;
}

.rwd-table td {
  display: block;
}

.rwd-table td:first-child {
  margin-top: .5em;
}

.rwd-table td:last-child {
  margin-bottom: .5em;
}

.rwd-table td:before {
  content: attr(data-th) ": ";
  font-weight: bold;
  width: 120px;
  display: inline-block;
  color: #000;
}

.rwd-table th,
.rwd-table td {
  text-align: left;
}

.rwd-table {
  color: #333;
  border-radius: .4em;
  overflow: hidden;
}

.rwd-table tr {
  border-color: #bfbfbf;
}

.rwd-table th,
.rwd-table td {
  padding: .5em 1em;
}
@media screen and (max-width: 601px) {
  .rwd-table tr:nth-child(2) {
    border-top: none;
  }
}
@media screen and (min-width: 600px) {
  .rwd-table tr:hover:not(:first-child) {
    /* background-color: #d8e7f3; */
  }
  .rwd-table td:before {
    display: none;
  }
  .rwd-table th,
  .rwd-table td {
    display: table-cell;
    padding: .25em .5em;
  }
  .rwd-table th:first-child,
  .rwd-table td:first-child {
    padding-left: 0;
  }
  .rwd-table th:last-child,
  .rwd-table td:last-child {
    padding-right: 0;
  }
  .rwd-table th,
  .rwd-table td {
    padding: 1em !important;
  }
}


