.main{
    display: flex;
    justify-content: center;
    align-items: center;
}
.profile-container {
    display: flex;
    position: absolute;
    flex-flow: column;
    height: 100%;
    background: #f25670;
    bottom: 0;
    right: 0;
    left: 0;
    top: 5%;
}
  
    .containercard {
      height: 395px;
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      flex-wrap: wrap;
    }
    .containercard1 {
      height: 290px;
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      flex-wrap: wrap;
    }
  
    .item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 250px;
      width: 250px;
      border-radius: 30px;
      background: #ffffff;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
      background-size: 150px;
      background-position: 50% 20%;
      background-repeat: no-repeat;
      cursor: pointer;
      color: #000000;
    }
    .item p {
      padding-top: 150px;
    }
    .item:hover {
      box-shadow: 10px 10px 10px crimson;
    }
  
    .item-1 {
      background-image: url('../images/available_organs.svg');
    }
    .item-2 {
      background-image: url('../images/donate_an_organ.svg');
    }
    .item-3 {
      background-image: url('../images/Group_22.svg');
    }
    .item-4 {
      background-image: url('../images/request_an_organ.svg');
    }
    .item-5 {
      background-image: url('../images/policy_compliance.svg');
      background-size: 200px;
      background-repeat: no-repeat;
    }
a{
    text-decoration:none;
}
.containercard>a{
    text-decoration:none;

}